.Box{
	position:relative;
    width:100px;
    z-index:2000!important;
	height:100px;
	transform-style:preserve-3d;
	transform-origin:center;
	transform:rotate(45deg);
	-webkit-animation:Loading 1s linear infinite;
	        animation:Loading 1s linear infinite;
}
.Box span{
	position:absolute;
    width:50px;
    z-index:2000!important;
	height:50px;
	-webkit-animation:MyLoading 1s ease-in-out infinite;
	        animation:MyLoading 1s ease-in-out infinite;
}
.Box span:first-of-type{
	left:0px;
	top:0px;
    background:#eb3b5a;
    z-index:2000!important;
}
.Box span:nth-of-type(2){
	right:0px;
	top:0px;
    background:#0fb9b1;
    z-index:2000!important;
}
.Box span:nth-of-type(3){
	left:0px;
	bottom:0px;
    background:#e1b12c;
    z-index:2000!important;
}
.Box span:last-of-type{
	right:0px;
	bottom:0px;
    background:#227093;
    z-index:2000!important;
}

@-webkit-keyframes Loading{
	0%,90% ,100%{
		width:100px;
		height:100px;
	}
	50%{
		width:150px;
		height:150px;	
	}
}

@keyframes Loading{
	0%,90% ,100%{
		width:100px;
		height:100px;
	}
	50%{
		width:150px;
		height:150px;	
	}
}


@-webkit-keyframes MyLoading{
	0%{
		transform:rotate(0deg);
	}
	100%{
		transform:rotate(90deg);
	}
}


@keyframes MyLoading{
	0%{
		transform:rotate(0deg);
	}
	100%{
		transform:rotate(90deg);
	}
}

